.button {
    user-select: none;
    cursor: pointer;
}

body {
    font-family: Noto Sans TC;
    user-select: none;
    background-color: #000;
    height: 100%;
    overflow: hidden;
}

canvas {
    margin-top: 50px;
}

#root {
    z-index: 100;
}

.app-layout {
    display: flex;
    flex-direction: column;
    z-index: 100;
    height: 100%;
    background-color: transparent;
}

::-webkit-scrollbar {
    width: 10px;
}

::-webkit-scrollbar-track {
    background: rgba(0, 0, 0, .1);
    border-radius: 5px;
}

::-webkit-scrollbar-thumb {
    background: rgba(0, 0, 0, .2);
    border-radius: 2px;
}

.slider {
    display: inline-flex;
    flex-direction: row;
    align-items: center;
    position: fixed;
    width: 150px;
    bottom: 10px;
    left: 10px;
    backdrop-filter: blur(8px);
    background-color: #424242;
    z-index: 1;
}

.function-bar {
    width: 100%;
    -webkit-box-shadow: 0px 0px 39px 6px rgba(41, 41, 41, 1);
    -moz-box-shadow: 0px 0px 39px 6px rgba(41, 41, 41, 1);
    box-shadow: 0px 0px 39px 6px rgba(41, 41, 41, 1);
    background-color: #424242;
    padding: 10px 10px 10px 10px;
    color: rgb(266, 266, 266);
    position: absolute;
    display: flex;
    flex-direction: row;
    align-items: center;
    font-family: Roboto;
}

.divider {
    background-color: rgb(126, 126, 126);
    width: 1px;
    height: 15px;
    margin-top: 5px;
    margin-bottom: 5px;
    margin-left: 10px;
    margin-right: 10px;
}